import React, { useState } from 'react';

function Home() {
  const [string, setString] = useState("");
  const [currentMovie, setCurrentMovie] = useState([]);

  const handleInput= (e)=>{
    e.preventDefault()
    setString(e.target.value)
    
    fetch(`https://api.themoviedb.org/3/search/movie?api_key=a3eca5539e3715256e80a664443dbf05&language=en-US&query=${e.target.value}&page=1&include_adult=true`)
    .then( data => data.json())
    .then(data => {
      if(data.error){
        setCurrentMovie([]);
      }else{
        setCurrentMovie(data.results)
      }
      console.log(data);
    });
  }

  return (
    <div className="home">
      <p>search movie</p>
      <input 
        type="text" 
        placeholder="search"
        onChange={handleInput}
        value={string}
      />
        {currentMovie.length > 0 && (
          <div>
            {currentMovie.map(movie =>(
              <div className="movieCard">
                <img src={`https://image.tmdb.org/t/p/w200${movie.poster_path}`} alt={`${movie.title} poster`}/>
                <div>
                  <input type="button" value="add"/>
                  <input type="button" value="remove"/>
                </div>
              </div>
            ))}
          </div>
        )}  
    </div>
  )
}

export default Home
